import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api/transaction/approval/rule";

class TransactionApprovalService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
const url = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllUsers = (start = 0, limit = 10, search = "") => {
    // /custom/api/user/profile/search?start=0&limit=10&userType=EMPLOYEE_USER
const url = `/custom/api/user/profile/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  getProductInfo = ( uid = "") => {
    const url = `${DEFAULT_PATH}/manage/uid/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllProductList = (start=0,limit=10) => {
    const url = `/custom/api/product/search?start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllIncomeSchemeList = (start=0,limit=10) => {
    const url = `/custom/api/income/scheme/product/search?start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  
  save = (obj: any) => {
    const url = `${DEFAULT_PATH}/manage`;
    return this.makePostRequestWithAuth(url, obj);
  };

  update = ( uid: string | undefined, obj: any ) => {
   
    const url = `${DEFAULT_PATH}/manage/${uid}`;
    return this.makePutRequestWithAuth(url, obj);
  };
  
  findById = (id: string) => {
    const url = `${DEFAULT_PATH}/uid/${id}`;
    return this.makeGetRequestWithAuth(url);
  };
  activate= (uid: string) =>{
    const url = `${DEFAULT_PATH}/activate/${uid}`;
    return this.makePutRequestWithAuth(url);
  }
  Inactivate= (uid: string) =>{
    const url = `${DEFAULT_PATH}/inactivate/${uid}`;
    return this.makePutRequestWithAuth(url);
  }
  
  modelToAPIParam = () => {
    console.log("object")
  };


  getAllRuleName=()=>{
    const url = `/custom/api/business/rule/search?start=0&limit=10`;
    return this.makeGetRequestWithAuth(url);
  }
  getListById=(start = 0, limit = 10, search = "")=>{
    const url = `${DEFAULT_PATH}/user/search?start=${start}&limit=${limit}${search}`;
    // /custom/api/transaction/approval/rule/user/search?start=0&limit=10&transactionApprovalRule.uid=ba31f977-56b5-4e1d-9204-879d8488d150
    return this.makeGetRequestWithAuth(url);
  }
}

export default new TransactionApprovalService();
